import apiAdress from "connectApi/api/apiAdress";

export const getAllAddresses = async (_id) => {
    try {
        const res = await apiAdress.getApiAdress(_id);
        const api = res.data;
        const result = api.map((item) => {
            const {
                billingAddress,
                isDefault,
                email,
                fullName,
                phoneNumber,
                province,
                _id,
            } = item;
            return {
                billingAddress,
                isDefault,
                email,
                fullName,
                phoneNumber,
                province,
                _id,
            };
        });
        return result;
    } catch (err) {
        console.log("li ly a ch khch hng");
    }
};

